<template>
	<div id="Page">
		<div class="pllie">
			<ul class="homer">
				<li v-for="(item,index) in perionth" @click="clikeTiong(item,index)" :class="isshow === item.permitId ? 'isshow' :''">
					{{ item.permitName }}
					<p class="soule"></p>
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
	export default {
		name:"PuBlicto",
		data() {
			return{
                isshow: 1,
				perionth:'',
				pentionger:'',
				sites: []
            }
		},
		components: {

		},
		created:function() {
			if (this.$route.path === '/Charging/Administration/QueryAdminist') {
					this.isshow = 1;
			} else if(this.$route.path === '/Charging/Administration/administration'){
				this.isshow = 2;
			} else if(this.$route.path === '/Charging/Administration/Application'){
				this.isshow = 3;
			} else if(this.$route.path === '/Charging/Administration/Addstatistics'){
				this.isshow = 4;
			}
			this.preiong();
		},
		activated:function(){
			this.preiong();
		},
		methods: {
			preiong: function(){
				this.perionth = this.$store.state.permissions.permissions;
				for(let key  in this.perionth){
					var permissions = this.perionth[key];
					this.pentionger = permissions.permitId
				}
			},
			clikeTiong(item,index){
				if(item.permitId == 1){
					if (this.$route.path === '/Charging/Administration/QueryAdminist') {
						return false;
					}
					this.$router.push({ path: '/Charging/Administration/QueryAdminist'});
				} else if(item.permitId == 2){
					if (this.$route.path === '/Charging/Administration/administration') {
						return false;
					}
					this.$router.push({ path: '/Charging/Administration/administration'});
				} else if(item.permitId == 3){
					if (this.$route.path === '/Charging/Administration/Application') {
						return false;
					}
					this.$router.push({ path: '/Charging/Administration/Application'});
				} else if(item.permitId == 4){
					if (this.$route.path === '/Charging/Administration/Addstatistics') {
						return false;
					}
					this.$router.push({ path: '/Charging/Administration/Addstatistics'});
				}
			}

		}
	}
</script>

